/**
 * Organisation data organisation_data_db_by_name.
 * @customfunction
 * @param {string} name .
 * @param {CustomFunctions.Invocation} invocation Invocation object.
 * @requiresAddress
 * @return status.
 */
// eslint-disable-next-line no-unused-vars, prettier/prettier
export async function organisation_classification_data_sectors( invocation): Promise<any> {
  const url = "https://api.thedatabusiness.net/api/coreapi/classification-data-db";
  try {
    var api_key = await get_api_key();
    const axiosPromise = await axios({
      method: "POST",
      headers: {},
      auth: {
        username: "user",
        password: api_key,
      },
      data: {
        classification_type: "sector",
      },
      url: url,
    });

    var result = axiosPromise.data.result;
    console.log(result);
    if (result == null) {
      return "No Results";
    }
    //sheet,row,column
    var location = await get_current_location(invocation);
    var curr_row = parseInt(location[1]);
    var curr_col_sec = location[2];

    console.log(location);
    for (let i = 0; i < result.length; i++) {
      var sector_data = result[i];
      var sector_name = sector_data.sector_name;
      curr_row++;
      await write_data_next_columns(location[0], "" + curr_row, curr_col_sec, [sector_name]);
      await write_data_next_columns(location[0], "" + curr_row, get_next_col(get_next_col(curr_col_sec)), [""]);
      await write_data_next_columns(location[0], "" + curr_row, get_next_col(curr_col_sec), [""]);

      console.log(sector_data);
      for (let j = 0; j < sector_data.types.length; j++) {
        var type_data = sector_data.types[j];
        var type_name = type_data.type_name;
        curr_row++;
        await write_data_next_columns(location[0], "" + curr_row, get_next_col(curr_col_sec), [type_name]);
        await write_data_next_columns(location[0], "" + curr_row, curr_col_sec, ["--------------->"]);
        await write_data_next_columns(location[0], "" + curr_row, get_next_col(get_next_col(curr_col_sec)), [""]);

        for (let k = 0; k < type_data.subtypes.length; k++) {
          var subtype_data = type_data.subtypes[k];
          var subtype_name = subtype_data.subtype_name;
          curr_row++;
          await write_data_next_columns(location[0], "" + curr_row, get_next_col(get_next_col(curr_col_sec)), [
            subtype_name,
          ]);
          await write_data_next_columns(location[0], "" + curr_row, curr_col_sec, ["--------------->"]);
          await write_data_next_columns(location[0], "" + curr_row, get_next_col(curr_col_sec), ["--------------->"]);
        }
      }
      curr_row++;
    }

    return "Successs";
  } catch (error) {
    // console.log(error)
    return error.name + " , " + error.message + " , " + url + " ";
  }
}

